import {Market, MARKETS, OpenOrders, Orderbook, TOKEN_MINTS, TokenInstructions,} from '@project-serum/serum';

import {PublicKey} from '@solana/web3.js';
import {
  MarketInfo
} from './types';
// Used in debugging, should be false in production
const _IGNORE_DEPRECATED = true;

export const USE_MARKETS: MarketInfo[] = _IGNORE_DEPRECATED
  ? MARKETS.map((m) => ({ ...m, deprecated: false }))
  : MARKETS;

export const findTVMarketFromAddress = (marketAddressString: string) => {
  for (  let markets of USE_MARKETS ) {
    if (markets.address.toBase58() === marketAddressString) {
      return markets.name;
    }
  }
  return 'SAMO/USDC';
};
